import React from 'react';
import {
  HashRouter as Router,
  Switch,
  Route,
  Redirect,
} from 'react-router-dom';
import Landing from './pages/Landing.jsx';

const App = () => {
  return (
    <>
      <Router>
        <Switch>
          <Route path="/" exact component={Landing}></Route>
          <Redirect to="/" />
        </Switch>
      </Router>
    </>
  );
};

export default App;
